@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.landing-description {
    font-size: 450%;
    font-family: 'Lobster', cursive;

    display: flex;
    justify-content: center;
    text-align: center;
    padding-left: 1rem;
    padding-right: 1rem;

    margin-top: 7rem;

    color:  rgb(253, 249, 249);
}

.landing-options {
    margin-top: 3rem;
    margin-bottom: 3rem;

    /* display: flex; */
    /* justify-content: space-evenly; */
    /* flex-direction: row; */
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.landing-button {
    color: inherit;
    text-decoration: inherit;

    padding: 1.5rem;
    font-size: 120%;

    border-radius: 15px;

    box-shadow: 5px 10px;
    height: fit-content;
    width: fit-content;

    text-align: center;
}

@keyframes landing-button-to-white {
    100% {
        background-color: rgb(253, 249, 249);
        border: solid #000 3px;
    }
}

.landing-middle-wrapper {
    padding-top: 3rem;
}

.landing-top-wrapper {
    border-bottom: solid #D9594C 10px;
    /* margin-bottom: 1; */
    background-image: url('https://10619-2.s.cdn12.com/rests/original/506_503860889.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 3rem;
}

.landing-wrapper-all {
    display: grid;
    grid-template-rows: 60vh 1rem;
}

.landing-middle-wrapper {
    
}

.landing-button-selected {
    animation: landing-button-to-white 1s forwards;
}

.landing-button:nth-child(1) {
    background-color: #e55934;
    margin-right: 1rem;
    margin-left: auto;
}

.landing-button:nth-child(2) {
    background-color: #CE8D66;
    margin-left: auto;
    margin-right: auto;
}

.landing-button:nth-child(3) {
    background-color: #B7B868;
    margin-left: 1rem;
    margin-right: auto;
}